import React, { useEffect } from "react";
import Card from "./Card";
import "./Projects.css";
import gn from "../images/guess-the-number.png";
import tdl from "../images/to-do-list.png";
import Aos from "aos";
import "aos/dist/aos.css";

const projectDetails = [
  {
    title: "Budgetify",
    img: "",
    description: "Budget App to keep track of income and expenses",
    link: "",
  },
  {
    title: "To-Do-List",
    img: tdl,
    description:
      "Basic To-Do-List App to create list of things to do and delete when done",
    link: "https://kalyan12344.github.io/To-Do-List/",
  },
  {
    title: "Guess The Number",
    img: gn,
    description: "A small game where you need to guess the exact random number",
    link: "https://kalyan12344.github.io/guess-a-number/",
  },
];
const Projects = () => {
  useEffect(() => {
    Aos.init({ duration: 2000 });
  }, []);
  return (
    <div className="project-section" data-aos="fade-right">
      {" "}
      <h1 className="project-title">MY PROJECTS</h1>
      <div className="projects">
        {projectDetails.map((p) => (
          <Card
            title={p.title}
            image={p.img}
            desc={p.description}
            link={p.link}
          ></Card>
        ))}
      </div>
    </div>
  );
};

export default Projects;
