import React, { useEffect } from "react";
import "./Home.css";
import image from "../images/pro-image.png";
import Aos from "aos";
import "aos/dist/aos.css";

const Home = () => {
  useEffect(() => {
    Aos.init({ duration: 2000 });
  }, []);
  return (
    <div className="home" data-aos="fade-left">
      <div className="intro">
        <span>Hello,</span>
        <h1>
          I<span style={{ color: "blue" }}>'</span>m Kalyan Keerthipat
          <span style={{ color: "blue" }}>i</span>
        </h1>
        <h2>Front-End Developer</h2>
        <h4>
          from <span>India</span>
        </h4>
      </div>
      <div className="image">
        <img src={image} alt="IMAGE" />
      </div>
    </div>
  );
};

export default Home;
