import React, { useEffect } from "react";
import Card from "./Card";
import "./Skills.css";
import html from "../images/html.png";
import css from "../images/css.png";
import javascript from "../images/JavaScript.png";
import react from "../images/react1.png";
import python from "../images/python.png";
import sql from "../images/sql.png";
import ps from "../images/peoplesoft.png";
import Aos from "aos";
import "aos/dist/aos.css";

let skills = [
  {
    title: "HTML",
    img: html,
  },
  {
    title: "CSS",
    img: css,
  },
  {
    title: "Javascript",
    img: javascript,
  },
  {
    title: "React",
    img: react,
  },
  {
    title: "Python",
    img: python,
  },
  {
    title: "SQL",
    img: sql,
  },
  {
    title: "Peoplesoft",
    img: ps,
  },
];
const Skills = () => {
  useEffect(() => {
    Aos.init({ duration: 2000 });
  }, []);
  return (
    <div className="skill-set" data-aos="fade-left">
      <h1 className="skill-title">My Skills</h1>
      <div className="skills">
        {" "}
        {skills.map((skill) => {
          // console.log(skill.img);
          return <img src={skill.img} className="skill-image" />;
        })}
      </div>
    </div>
  );
};

export default Skills;
