import React, { useEffect } from "react";
import "./Card.css";
import Aos from "aos";
import "aos/dist/aos.css";
// import image from "../images/profile-image.jpg";
const Card = (props) => {
  // useEffect(() => {
  //   Aos.init({ duration: 2000 });
  // }, []);
  return (
    <div className="card">
      <h3 className="card-title">{props.title}</h3>
      <img src={props.image} alt={props.alternate} className="card-image" />
      <p className="card-desc">{props.desc}</p>
      <a href={props.link} className="card-link" target="_blank">
        &#10140;
      </a>
    </div>
  );
};

export default Card;
